module.exports = [[module.id, "/* FOLHAS DE ESTILO GLOBAIS - Afetam toda a aplicação. */\r\n\r\n\r\n/* Cores do tema */\r\n\r\n\r\n:root {\r\n    --color-1: rgb(0, 255, 0);\r\n    --color-2: rgb(0, 240, 0);\r\n    --color-3: rgb(0, 224, 0);\r\n    --color-4: rgb(0, 192, 0);\r\n    --color-5: rgb(0, 128, 0);\r\n    --color-6: rgb(0, 64, 0);\r\n    --color-7: rgb(0, 32, 0);\r\n    --color-8: rgb(0, 16, 0);\r\n    --link-hover: orangered;\r\n    --box-border-px: 2px;\r\n    --box-border-tik: 8px;\r\n    --box-border: var(--box-border-px) solid var(--color-1);\r\n    --head-nav-foot-bg: rgba(0, 64, 0, .5);\r\n    --main-bg: rgba(0, 32, 0, .9);\r\n    --menu-drop-bg: rgba(0, 64, 0, .9);\r\n    --list-bg: rgba(0, 96, 0, .5);\r\n}\r\n\r\n\r\n/* CSS reset */\r\n\r\n\r\n* {\r\n    box-sizing: border-box;\r\n    outline: none;\r\n}\r\n\r\n\r\nhtml,\r\nbody {\r\n    width: 100%;\r\n    padding: 0;\r\n    margin: 0;\r\n    font-family: 'Comic Neue', cursive;\r\n    font-size: 16px;\r\n}\r\n\r\n\r\nbody {\r\n    background-image: url('bg01.jpg');\r\n    background-attachment: fixed;\r\n    background-position: bottom left;\r\n    background-repeat: no-repeat;\r\n    background-size: cover;\r\n    background-color: black;\r\n    color: var(--color-1);\r\n}\r\n\r\n\r\na {\r\n    color: var(--color-1);\r\n    text-decoration: none;\r\n    border-bottom: 1px dotted var(--color-1);\r\n    display: inline-block;\r\n}\r\n\r\n\r\na:hover {\r\n    color: var(--link-hover);\r\n    border-bottom: 1px dotted var(--link-hover);\r\n}\r\n\r\n\r\n/* Centraliza e limita o crescimento da tela. */\r\n\r\n\r\napp-root {\r\n    min-height: 100%;\r\n    display: flex;\r\n    flex-direction: column;\r\n    width: 100%;\r\n    max-width: 960px;\r\n    margin: auto;\r\n}\r\n\r\n\r\n/* Sticky footer */\r\n\r\n\r\nhtml,\r\nbody,\r\napp-root {\r\n    position: relative;\r\n    height: 100%;\r\n}\r\n\r\n\r\n/* Responsivo */\r\n\r\n\r\n@media (min-width: 640px) {\r\n    body {\r\n        background-position: center;\r\n    }\r\n}\r\n\r\n\r\n/* Botões genéricos */\r\n\r\n\r\n.basic-button {\r\n    display: inline-block;\r\n    padding: .5rem 1rem;\r\n    color: var(--color-8);\r\n    background: linear-gradient(var(--color-2), var(--color-4));\r\n    border-radius: 0rem;\r\n    border: none;\r\n    text-transform: uppercase;\r\n}\r\n\r\n\r\n.basic-button:hover {\r\n    color: var(--color-1);\r\n    background: linear-gradient(var(--color-5), var(--color-7));\r\n    border: none;\r\n}\r\n\r\n\r\n/* Classes de uso geral */\r\n\r\n\r\n.center {\r\n    text-align: center;\r\n}\r\n\r\n\r\n.small {\r\n    font-size: small;\r\n}\r\n\r\n\r\n/* Bordas das caixas */\r\n\r\n\r\n.hborder {\r\n    border-left: var(--box-border);\r\n    border-right: var(--box-border);\r\n}\r\n\r\n\r\n.vborder {\r\n    border-top: var(--box-border);\r\n    border-bottom: var(--box-border);\r\n}\r\n\r\n\r\n.hborder,\r\n.vborder {\r\n    position: relative;\r\n    box-shadow: 5px 5px 5px rgba(0, 0, 0, .7);\r\n}\r\n\r\n\r\n.hbd {\r\n    position: absolute;\r\n    width: var(--box-border-tik);\r\n    height: var(--box-border-px);\r\n    background: var(--color-1);\r\n}\r\n\r\n\r\n.vbd {\r\n    position: absolute;\r\n    height: var(--box-border-tik);\r\n    width: var(--box-border-px);\r\n    background: var(--color-1);\r\n}\r\n\r\n\r\n.tl {\r\n    top: 0;\r\n    left: 0;\r\n}\r\n\r\n\r\n.tr {\r\n    top: 0;\r\n    right: 0;\r\n}\r\n\r\n\r\n.br {\r\n    bottom: 0;\r\n    right: 0;\r\n}\r\n\r\n\r\n.bl {\r\n    bottom: 0;\r\n    left: 0;\r\n}\r\n/*# sourceMappingURL=data:application/json;base64, */", '', '']]